@import  'https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css';


.App {
  text-align: center;
}

td{
  width: 180px;
  border: 1px solid black;
  border-radius: 3px;
  vertical-align: middle;

}

.login__container {
  width: 300px;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
}
.login__inputForm {
  margin: 8px;
}


.boss__createTicket {
  float: right;
}
.boss_container {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
.boss__grade {
   width: 100%;
   height: 10px;
   float: none;
}
.boss__table {
  padding-top: 30px;
  height: auto;
  width: 100%;
  clear: both;
  margin-bottom: 50px;
}
.boss__uppendAdditionalTask{
  width: 100%;

}
.boss__additionalTasksHeader {
  float: left;
}
.boss__inputBlock{
  padding-top: 30px;
  height: auto;
  height:  160px;
}
.boss__input {
  margin:3px;
  float: left;
}
.boss__inputGroupButton  {
  width: 300px;
}
.boss__displayNO {
  display: none;
}
label {
  margin-top: 6px;
}

.profile__container {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1vw;
}
.profile__input {
  
}
.profile__hGraphics {
  margin-top: 40px;
}
.profile__XTitle {
  text-align: left;
}
.profile__YTitle {
  text-align: right;
}
.profile__title {
  width: 100%;
}
.profile__regularPlans{
  display: inline-block;
}
.profile__titleButtons{

  display: inline-block;
}
.profile__titleButton {
  display: block;
  margin: 80px;
}
.profile__titlePlans {
  text-align: left;
}
.profile__countPlans {
  text-align: right;
}
.profile__table {
    display: block;
    float: left;
    margin-bottom: 30px;
}
.profile__agenda {
    display: block;
    margin-left: 30px;
    float: right;
    margin-bottom: 30px;

}
.profile__smartTicket {
  border-radius: 10px;
  border: 1px solid black;
  min-width: 80px;
  min-height: 50px;
  padding: 10px;
  margin: 10px;
}
.leader__selectPersone{
  width: 150px;
  margin-top: 10px;
}
.leader__additionalTasksHeader {
  float: left;
}
.leader__uppendAdditionalTask {
  margin-top:10px;
  margin-bottom: 10px;
}
.leader__selectLineChart {
  float: left;
  width: 200px;
}
.leader__lineChartBlock {
  padding-top: 130px;
  display: block;
}
.leader__secondBlock {
  width: 100%;
  height: auto;
}
